import React, { useState } from "react";
import { Grid, Card, CardContent, CardActionArea } from "@material-ui/core";
import PrintStatus from "./PrintStatus/PrintStatus";

import FileManager from "../Common/FileManager/FileManager";
import "./Print.css";

import { Button, Typography } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";

const useStyles = makeStyles({
  buttonContainer: {
    display: "flex",
    justifyContent: "center",
    marginTop: "10px"
  },
  textCenter: {
    textAlign: "center",
    marginTop: "10px"
  },
  card: {
    margin: "32px",
    border: "2px solid rgba(0, 0, 0, 0.12)",
    textAlign: "center"
  },
  cardContent: {
    display: "flex",
    flexDirection: "row",
    justifyContent: "center",
    alignItems: "center"
  }
});

const Print = ({ lastMessage, setDisabledNav }) => {
  const classes = useStyles();
  let [state, setState] = useState({
    extruder: 0,
    bed: 0,
    connected: false,
    feed: "1",
    file: -1,
    files: [],
    printing: false,
    progress: 0,
    paused: false
  });
  const [file, uploadedFile] = useState(false);
  const [status, setStatus] = useState(null);
  const [printState, setPrintState] = useState(null);
  const [jobState, setJobState] = useState(null);
  const [progressState, setProgressState] = useState(null);
  const [fileDrawerOpen, setFileDrawerOpen] = useState(false);
  const [fileLocation, setFileLocation] = useState("");

  const fetchFiles = (location) => {
    setFileLocation(location);
    setFileDrawerOpen(true);
  };

  React.useEffect(() => {
    if (
      typeof lastMessage !== "undefined" &&
      lastMessage &&
      typeof lastMessage.data !== "undefined" &&
      lastMessage.data.length > 1
    ) {
      let json = JSON.parse(lastMessage.data.substring(1));
      console.log(json);
      if (json[0].hasOwnProperty("current") && json[0].current.state) {
        console.log(json[0].current.state);
        setPrintState(json[0].current.state);
        console.log(printState);
      }

      if (json[0].hasOwnProperty("current") && json[0].current.job) {
        console.log(json[0].current.job);
        setJobState(json[0].current.job);
        console.log(jobState);
      }

      if (json[0].hasOwnProperty("current") && json[0].current.progress) {
        setProgressState(json[0].current.progress);
        console.log(progressState);
      }
    }
  }, [lastMessage]);

  return (
    <Grid>
      {progressState &&
      jobState &&
      jobState.file.name &&
      progressState.completion < 100 &&
      printState &&
      printState.text !== "Operational" ? (
        <PrintStatus
          lastMessage={lastMessage}
          progressState={progressState}
          setDisabledNav={setDisabledNav}
          statusText={printState.text}
          fileName={jobState.file.display}
          state={state}
        />
      ) : (
        <Grid>
          <Card className={classes.card} onClick={() => fetchFiles("local")}>
            <CardActionArea>
              <CardContent className={classes.cardContent}>
                <Typography variant="h4">Printer Storage</Typography>
              </CardContent>
            </CardActionArea>
          </Card>
          <Card className={classes.card} onClick={() => fetchFiles("usb")}>
            <CardActionArea>
              <CardContent className={classes.cardContent}>
                <Typography variant="h4">USB Media</Typography>
              </CardContent>
            </CardActionArea>
          </Card>
          <FileManager
            setDrawerOpen={setFileDrawerOpen}
            drawerOpen={fileDrawerOpen}
            fileLocation={fileLocation}
          />
          {/*<div className="Files">
            <label className="Files-Upload">
              <span>Upload</span>
              <input
                type="file"
                onChange={async (e) => {
                  let data = new FormData();
                  console.log(e.target.files);
                  e.persist();
                  data.append('file', e.target.files[0]);
                  // Upload the file
                  await fetch(
                    `http://localhost:8080/api/v1/files/${e.target.files[0].name}`,
                    { method: 'POST', body: data }
                  );
                  // Print the file
                  await fetch(
                    `http://localhost:8080/api/v1/job/${e.target.files[0].name}`,
                    {
                      method: 'POST',
                      headers: { 'Content-Type': 'application/json' },
                    }
                  );
                  state.printing = true;
                  uploadedFile(true);
                  setState({
                    files: [...state.files, e.target.files[0].name],
                    file: 0,
                  });
                }}
              ></input>
            </label>
              </div>*/}
        </Grid>
      )}
      <div>
        <Grid>
          {printState &&
            jobState &&
            (jobState.file && typeof jobState.file.display !== "undefined" ? (
              <div>
                <div className={classes.buttonContainer}>
                  {progressState &&
                    (progressState.completion == null ||
                      progressState.completion == 100) &&
                    jobState &&
                    jobState.file.display && (
                      <>
                        <div className={classes.textCenter}>
                          {/*`${printState.text} : ${jobState.file.display}`*/}
                        </div>
                        {/* <Button
                          color="primary"
                          variant="contained"
                          onClick={() => {
                            fetch(
                              `http://localhost:8080/api/v1/job/${jobState.file.name}`,
                              {
                                method: "POST",
                                headers: { "Content-Type": "application/json" },
                              }
                            );
                          }}
                        >
                          Print
                        </Button> */}
                      </>
                    )}
                </div>
              </div>
            ) : (
              <span>Not Printing</span>
            ))}
        </Grid>
      </div>
    </Grid>
  );
};

export default Print;
