import React, { useState, useEffect } from "react";
import { withStyles } from "@material-ui/core/styles";
import { Container, Grid, Typography } from "@material-ui/core";
import { Button, LinearProgress } from "@material-ui/core";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPause, faPlay, faStop } from "@fortawesome/free-solid-svg-icons";
import { PlayArrow, Pause, Stop } from "@material-ui/icons";

import TutorialThermometer from "../../Tutorials/TutorialThermometer/TutorialThermometer";
import TempContext from "../../../TempContext";

import { makeStyles } from "@material-ui/core/styles";
const useStyles = makeStyles({
  buttonContainer: {
    display: "flex",
    justifyContent: "center",
    marginTop: "10px"
  },
  textCenter: {
    textAlign: "center",
    marginTop: "10px"
  }
});

const PrintStatus = ({
  lastMessage,
  progressState,
  setDisabledNav,
  state,
  statusText,
  fileName
}) => {
  const classes = useStyles();
  const [paused, setPaused] = useState(false);
  const [value, setValue] = useState(0);

  let sendGcode = function (gcode) {
    fetch(`http://localhost:8080/api/v1/send`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        gcode: gcode
      })
    });
  };

  useEffect(() => {
    if (progressState.completion !== value) {
      setValue(progressState.completion);
      console.log(value);
    }
  }, [progressState]);

  return (
    <>
      <Container>
        <Typography
          variant="h4"
          style={{ marginTop: 35, width: "100%", textAlign: "center" }}
        >
          {statusText}: {fileName}
        </Typography>
        <div className="print-status-bar" style={{ marginTop: 25 }}>
          <BorderLinearProgress variant="determinate" value={value} />
          <Typography
            variant="h6"
            style={{ width: "100%", textAlign: "center" }}
          >
            {value ? value.toFixed(1) : "0"}%
          </Typography>
        </div>
        <div style={{ marginTop: 50 }}>
          <TempContext.Consumer>
            {({ bed, nozzle, bedTarget, nozzleTarget }) => (
              <Grid container>
                <Grid item container xs={6} direction="column">
                  <Grid item style={{ marginBottom: -100 }}>
                    <div style={{ transform: "scale(.6)" }}>
                      <TutorialThermometer min={0} max={120} value={bed} />
                    </div>
                  </Grid>
                  <Grid item style={{ textAlign: "center" }}>
                    <h4>Target: {bedTarget}</h4>
                    <h4>Bed</h4>
                  </Grid>
                </Grid>
                <Grid item container xs={6} direction="column">
                  <Grid item style={{ marginBottom: -100 }}>
                    <div style={{ transform: "scale(.6)" }}>
                      <TutorialThermometer min={0} max={300} value={nozzle} />
                    </div>
                  </Grid>
                  <Grid item style={{ textAlign: "center" }}>
                    <h4>Target: {nozzleTarget}</h4>
                    <h4>Nozzle</h4>
                  </Grid>
                </Grid>
              </Grid>
            )}
          </TempContext.Consumer>
        </div>
        <div className="print-btn-container" style={{ marginTop: 50 }}>
          <Button
            color="primary"
            variant="contained"
            onClick={() => {
              setPaused((prevState) => !prevState);
              fetch(`http://localhost:8080/api/v1/job`, {
                method: "PATCH",
                headers: { "Content-Type": "application/json" },
                body: JSON.stringify({
                  file: state.files[state.file]
                })
              });
            }}
            startIcon={!paused ? <Pause /> : <PlayArrow />}
          >
            {!paused ? "Pause" : "Play"}
          </Button>
          <Button
            color="secondary"
            variant="contained"
            onClick={() => {
              fetch(`http://localhost:8080/api/v1/job`, {
                method: "DELETE",
                headers: { "Content-Type": "application/json" },
                body: JSON.stringify({
                  file: state.files[state.file]
                })
              });
            }}
            startIcon={<Stop />}
          >
            Stop
          </Button>
        </div>
      </Container>
    </>
  );
};

const BorderLinearProgress = withStyles((theme) => ({
  root: {
    height: 14,
    borderRadius: 7
  },
  colorPrimary: {
    backgroundColor: "#606060"
  },
  bar: {
    borderRadius: 7,
    backgroundColor: "#ACC12F"
  }
}))(LinearProgress);

export default PrintStatus;
