import React from "react";
import "./style.css";

import TempContext from "../../../TempContext";
import { Button, withStyles } from "@material-ui/core";
import { Power, PowerOff } from "@material-ui/icons";

import {
  GCodeBuilder,
  GEnableAllMotors,
  GDisableAllMotors
} from "../../Tutorials/GCodeBuilder";

class StepperDisable extends React.Component {
  constructor(props) {
    super(props);
  }

  static contextType = TempContext;

  disableMotors() {
    let gcode = new GCodeBuilder();
    gcode.addCommand(GDisableAllMotors());
    this.sendGcode(gcode.build());
    this.context.setSteppersEnabled(false);
  }

  enableMotors() {
    let gcode = new GCodeBuilder();
    gcode.addCommand(GEnableAllMotors());
    this.sendGcode(gcode.build());
    this.context.setSteppersEnabled(true);
  }

  sendGcode(gcode) {
    fetch(`http://localhost:8080/api/v1/send`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        gcode: gcode
      })
    });
  }

  render() {
    if (!this.context.steppersEnabled) {
      return (
        <div style={{ width: "100%", textAlign: "center", marginTop: 50 }}>
          <EnableButton
            startIcon={<Power />}
            color="primary"
            variant="contained"
            onClick={() => {
              this.enableMotors();
            }}
          >
            Enable Motors
          </EnableButton>
        </div>
      );
    } else {
      return (
        <div style={{ width: "100%", textAlign: "center", marginTop: 50 }}>
          <DisableButton
            startIcon={<PowerOff />}
            color="primary"
            variant="contained"
            onClick={() => {
              this.disableMotors();
            }}
          >
            Disable Motors
          </DisableButton>
        </div>
      );
    }
  }
}

const DisableButton = withStyles({
  root: {
    width: 200,
    backgroundColor: "#B01C34",
    "&:hover": {
      backgroundColor: "#B01C34"
    }
  }
})(Button);

const EnableButton = withStyles({
  root: {
    width: 200,
    backgroundColor: "#679436",
    "&:hover": {
      backgroundColor: "#679436"
    }
  }
})(Button);

export default StepperDisable;
